@import "~@/../scss/style.scss";







































































































































































































































// user message
#user-info {
    float: right;
    outline: none;
    height: 60px;
    border: none;
    background: none;
    font-weight: Bold;
    letter-spacing: 0.14px;
    opacity: 1;
    outline: none;
    padding: 0 12px;
    margin-top: 1rem;

    &:hover {
        cursor: pointer;
    }
}

.popover {
    -webkit-box-shadow: 0 0 15px #cecece;
    -moz-box-shadow: 0 0 15px #cecece;
    box-shadow: 0px 3px 10px #00000029;
    border: none;

    margin-top: -1.6rem;
    margin-right: -0.96rem;
    width: 13rem;

    ::v-deep .arrow {
        display: none;
    }

    .user-container {
        width: 100%;
        padding: 1rem;
        padding-top: 0;

        .col {
            align-self: center;
            display: flex;
            justify-content: center;
            text-align: center;
        }

        img {
            width: 5rem;
            border-radius: 50%;
        }

        .user-account {
            width: 100%;
            font-size: 1.15rem;
            font-weight: bold;
            margin-top: 1rem;
            margin-bottom: 2rem;
            word-wrap: break-word;
        }

        .user-name {
            margin-top: 1rem;
        }

        .a-link {
            display: flex;
        }

        .logout {
            padding: 0 1rem 0.2rem 1rem;
            background-color: #537895;
            margin-top: 2rem;
        }
    }
}
