@import "~@/../scss/style.scss";




















































































































































@import '../scss/style';

.bridle {
    height: 100vh;
    background: #f8f8f8 0% 0% no-repeat padding-box;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    flex-flow: column nowrap;
    overflow: hidden;
    display: flex;

    &__content {
        position: relative;
        margin-top: $heightHeader;
        margin-left: $widthNav;
        height: 100vh;
        overflow-x: auto;
        overflow-y: auto;
        padding: 30px;
        transition: all 0.3s;

        &--hide {
            overflow-x: hidden;
            margin-left: 0;
        }
    }

    &__footer {
        transition: all 0.3s;

        &--hide {
            margin-left: 0 !important;
        }
    }

    &__nav {
        transition: all 0.3s;
        left: 0;

        &--hide {
            overflow-x: hidden;
            left: -$widthNav;
        }
    }
}
