@import "~@/../scss/style.scss";























































































































































































































































.i18n-main {
    position: relative;

    img {
        width: 1.7rem;
        height: 1.7rem;
        border-radius: 50%;
    }

    .i18n {
        position: relative;

        &:hover {
            cursor: pointer;
        }
    }
}

.selected-i18n,
.selected-i18n:hover {
    background-color: #7d858c;
    font-weight: bold;
}

.cursor-default {
    cursor: default;
}

.popover {
    -webkit-box-shadow: 0 0 15px #cecece;
    -moz-box-shadow: 0 0 15px #cecece;
    box-shadow: 0px 3px 10px #00000029;
    border: none;

    margin-top: 0.65rem;
    margin-right: 2.1rem;
    width: 10rem;

    ::v-deep .popover-body {
        padding: 0.5rem 0;

        .language-data-box {
            max-height: 30rem;
            overflow-y: auto;
            text-align: conter;
            cursor: pointer;

            .language-data {
                padding: 0.5rem 1.5rem 0.5rem 2.5rem;
                font-size: 1rem;

                &:hover {
                    background-color: #337ab7;
                    color: #fff;
                    padding: 0.5rem 1.5rem 0.5rem 2.5rem;
                }
            }

            .language-data-selected,
            .language-data-selected:hover {
                padding: 0.5rem 1.5rem 0.5rem 2.5rem;
                font-size: 1rem;
                background-color: #7d858c;
                color: #000;
            }
        }
    }

    ::v-deep .arrow {
        display: none;
    }
}
