@import "~@/../scss/style.scss";










































































































.bell-main {
    position: relative;

    .hover-tip {
        font-size: 0.6rem;
        background-color: #537895;
        color: red;
        padding: 0.5rem;
        height: 1rem;
        margin-top: 0rem;
    }

    .bell {
        position: absolute;
        text-align: center;

        &:hover {
            cursor: pointer;
        }

        i {
            font-size: 1.7rem;
        }

        .unread-event {
            position: absolute;
            top: 0.8rem;
            right: -0.5rem;
            background-color: red;
            border-radius: 40%;
            border: 0.5px solid #fff;
            width: 1.25rem;
            height: 1.1rem;
            padding: 2px;

            .count {
                position: absolute;
                top: -1.5rem;
                right: 0.2rem;
                width: 0.8rem;
                height: 0.8rem;
                font-size: 0.5rem;
                font-weight: bold;
                color: white;
                padding: 2px 2px 1.5px 0px;
            }
        }

        .unread-event-three {
            position: absolute;
            top: 0.9rem;
            right: -1rem;
            background-color: red;
            border-radius: 0.5rem;
            border: 0.5px solid #fff;
            width: 1.8rem;
            height: 1rem;
            padding: 2px;

            .count {
                position: absolute;
                top: -1.6rem;
                right: 0.6rem;
                width: 0.8rem;
                height: 0.8rem;
                font-size: 0.5rem;
                font-weight: bold;
                color: white;
                padding: 2px 2px 1.5px 0px;
            }
        }
    }
}

.tooltip {
    ::v-deep .tooltip-inner {
        background-color: #537895;
        font-size: 0.8rem;
    }

    ::v-deep .arrow {
        display: none;
    }

    .tooltip-hover {
        margin-top: -1rem !important;
    }
}

.popover {
    -webkit-box-shadow: 0 0 15px #cecece;
    -moz-box-shadow: 0 0 15px #cecece;
    box-shadow: 0px 3px 10px #00000029;
    border: none;

    margin-top: -0.6rem;
    margin-right: 2.125rem;
    width: 17.5rem;

    ::v-deep .popover-body {
        padding: 0;

        .bell-data-box {
            max-height: 30rem;
            overflow-y: scroll;
            overflow-x: hidden;
            .bell-data {
                padding: 1rem 2rem;
                font-size: 1rem;

                .ellipsis {
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }

                &:hover {
                    background-color: #f1f1f1;
                    padding: 1rem 2rem;
                    cursor: pointer;
                }
            }
        }
    }

    ::v-deep .arrow {
        display: none;
    }
}
